import App from './App.svelte';

import { makeConnectedStore } from 'connectome/stores';

const port = 7780;

const protocol = 'dmtapp';
const lane = 'connect';

const { connected, state, api } = new makeConnectedStore({ port, protocol, lane });

const app = new App({
  target: document.body,
  props: {
    connected,
    state,
    api
  }
});

export default app;
