<script>
  import { Dialog, Button } from 'dmt-frontend-components';
  import { createEventDispatcher } from 'svelte';

  const dispatch = createEventDispatcher();

  export let show;
  export let connection;

  let newConnection;

  $: if (!show) {
    newConnection = '';
  }

  $: title = `Update "${connection}"`;
</script>

<Dialog bind:show {title}>
  <form on:submit|preventDefault={() => dispatch('updateConnection', newConnection)}>
    <!-- svelte-ignore a11y-autofocus -->
    <input bind:value={newConnection} type="text" autofocus placeholder="Enter new device name or address" />
    <div class="form-actions">
      <Button type="submit" variant="primary">Update</Button>
    </div>
  </form>
</Dialog>

<style>
  input {
    width: 100%;
    margin-top: 0.25rem;
    margin-bottom: 0.75rem;
  }

  .form-actions {
    text-align: right;
  }
</style>
