<script>
  import { createEventDispatcher } from 'svelte';
  import { SearchableList } from 'dmt-frontend-components';

  const dispatch = createEventDispatcher();

  export let devices = [];
  export let selectedDevice = '';
</script>

<SearchableList
  items={devices}
  searchPlaceholder="Search devices..."
  searchAutofocus
  noItemsText="No devices found"
  on:selectItem={e => dispatch('selectDevice', e.detail)}
>
  <div class="item" slot="item" let:item class:highlight={selectedDevice === item}>
    <span>
      {item}
    </span>
    {#if selectedDevice === item}
      <span class="dot" />
    {/if}
  </div>
</SearchableList>

<style>
  .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: currentColor;
    opacity: 0.5;
  }

  .highlight {
    color: rgb(var(--dmt-cool-cyan-rgb));
  }
</style>
