<script>
  import { Dialog, Button } from 'dmt-frontend-components';
  import { createEventDispatcher } from 'svelte';

  const dispatch = createEventDispatcher();

  export let show;

  let connection;

  $: if (!show) {
    connection = '';
  }
</script>

<Dialog bind:show title="Add connection">
  <form on:submit|preventDefault={() => dispatch('addConnection', connection)}>
    <!-- svelte-ignore a11y-autofocus -->
    <input bind:value={connection} type="text" autofocus placeholder="Enter device name or address" />
    <div class="form-actions">
      <Button type="submit" variant="primary">Add</Button>
    </div>
  </form>
</Dialog>

<style>
  input {
    width: 100%;
    margin-top: 0.25rem;
    margin-bottom: 0.75rem;
  }

  .form-actions {
    text-align: right;
  }
</style>
