<script>
  import { Button, Select } from 'dmt-frontend-components';
  import AddConnectionDialog from '../components/AddConnectionDialog.svelte';
  import UpdateConnectionDialog from '../components/UpdateConnectionDialog.svelte';
  import Logo from '../components/Logo.svelte';
  import ConnectionsTable from '../tables/ConnectionsTable.svelte';
  import DeviceList from '../components/DeviceList.svelte';

  export let state;
  export let api;

  let showAddConnectionDialog = false;
  let showUpdateConnectionDialog = false;
  let updatingConnectionId = '';

  $: deviceDirs = state.devices.map(v => v.deviceName);

  let selectedDeviceDir = '';

  $: if (!selectedDeviceDir && deviceDirs.length) {
    selectedDeviceDir = deviceDirs[0];
  }

  // NOTE: deviceConnections is currently an array of string (deviceName or address)
  let deviceConnections = [];
  $: {
    const device = state.devices.find(v => v.deviceName === selectedDeviceDir);

    if (device) {
      deviceConnections = device.connect || [];
    } else {
      deviceConnections = [];
    }
  }

  function handleRowEdit(e) {
    updatingConnectionId = e.detail;
    showUpdateConnectionDialog = true;
  }

  function handleRowRemove(e) {
    removeConnection(e.detail);
  }

  function handleAddConnection(e) {
    addConnection(e.detail);
    showAddConnectionDialog = false;
  }

  function handleUpdateConnection(e) {
    updateConnection(updatingConnectionId, e.detail);
    showUpdateConnectionDialog = false;
  }

  const apiName = 'connectome:build';

  function addConnection(connectionId) {
    api(apiName).call('addConnection', { deviceDir: selectedDeviceDir, connectionId });
  }

  function updateConnection(connectionId, newConnectionId) {
    api(apiName).call('updateConnection', { deviceDir: selectedDeviceDir, connectionId, newConnectionId });
  }

  function removeConnection(connectionId) {
    api(apiName).call('removeConnection', { deviceDir: selectedDeviceDir, connectionId });
  }
</script>

<div class="wrapper">
  <Logo />
  <div class="content">
    <div class="left-content">
      <DeviceList devices={deviceDirs} selectedDevice={selectedDeviceDir} on:selectDevice={e => (selectedDeviceDir = e.detail)} />
    </div>
    <div class="right-content">
      <section class="device-list-info">
        <p class="description">Select a device from the sidebar to manage connections</p>
        {#if selectedDeviceDir}
          <p class="selected-description">Selected device: <strong>{selectedDeviceDir}</strong></p>
        {/if}
      </section>
      <section class="device-select">
        <p class="description">Select a device to manage connections</p>
        <div class="select">
          <Select bind:value={selectedDeviceDir} options={deviceDirs} placeholder="Select a device" />
        </div>
      </section>
      {#if selectedDeviceDir}
        <section class="connections">
          <div class="connections-header">
            <h1>Connections</h1>
            <Button size="sm" variant="primary" on:click={() => (showAddConnectionDialog = true)}>+ Add connection</Button>
          </div>
          <ConnectionsTable data={deviceConnections} on:rowEdit={handleRowEdit} on:rowRemove={handleRowRemove} />
        </section>
      {/if}
    </div>
  </div>
</div>

<AddConnectionDialog bind:show={showAddConnectionDialog} on:addConnection={handleAddConnection} />
<UpdateConnectionDialog bind:show={showUpdateConnectionDialog} connection={updatingConnectionId} on:updateConnection={handleUpdateConnection} />

<style>
  .wrapper {
    padding: 1rem;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .content {
    flex: 1;
    display: flex;
    margin-top: 1rem;
  }

  .left-content,
  .device-list-info {
    display: none;
  }

  .right-content {
    /* Fix to connections table width */
    width: 360px;
    overflow-y: auto;
  }

  .device-list-info {
    text-align: center;
  }

  .device-select {
    text-align: center;
  }

  .description {
    margin: 0;
    margin-bottom: 0.8rem;
  }

  .selected-description {
    margin: 2rem 0;
    font-size: 1.125rem;
  }

  .select {
    display: inline-block;
    width: 250px;
  }

  .connections {
    margin-top: 1rem;
  }

  .connections-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.7rem;
  }

  .connections-header h1 {
    margin: 0;
  }

  @media (min-width: 680px) {
    .content {
      overflow: hidden;
    }

    .left-content,
    .device-list-info {
      display: block;
    }

    .device-select {
      display: none;
    }

    .right-content {
      margin-left: 1.3rem;
    }
  }
</style>
